'use client';

import { Button, Link, useIsHydrated } from "@heroui/react";
import { useUserStore } from "../../stores/user/userStore";
import { useState } from "react";
import NextLink from "next/link";
import { ConfirmModal } from "../../components/common";


export default function UsersPage() {
    const hydrated = useIsHydrated()
    const {
        searchQuery,
        roleFilter,
        statusFilter,
        setSearchQuery,
        setRoleFilter,
        setStatusFilter,
        deleteUser,
        getFilteredUsers
    } = useUserStore();

    const filteredUsers = getFilteredUsers();
    const [currentPage, setCurrentPage] = useState(1);
    const [deleteModal, setDeleteModal] = useState<{ isOpen: boolean; userId: string; userName: string }>({
        isOpen: false,
        userId: '',
        userName: ''
    });
    const itemsPerPage = 10;
    const totalPages = Math.ceil(filteredUsers.length / itemsPerPage);

    const paginatedUsers = filteredUsers.slice(
        (currentPage - 1) * itemsPerPage,
        currentPage * itemsPerPage
    );

    const getRoleBadgeColor = (role: string) => {
        switch (role) {
            case 'Administrator': return 'bg-purple-600/20 text-purple-400 border-purple-600/30';
            case 'Field Supervisor': return 'bg-orange-600/20 text-orange-400 border-orange-600/30';
            case 'Engineer': return 'bg-blue-600/20 text-blue-400 border-blue-600/30';
            default: return 'bg-gray-600/20 text-gray-400 border-gray-600/30';
        }
    };

    const handleDelete = (id: string, name: string) => {
        setDeleteModal({ isOpen: true, userId: id, userName: name });
    };

    const confirmDelete = () => {
        deleteUser(deleteModal.userId);
        setDeleteModal({ isOpen: false, userId: '', userName: '' });
    };

    const handleCancelDelete = () => {
        setDeleteModal({ isOpen: false, userId: '', userName: '' });
    };

    if (!hydrated) {
        return null;
    }

    return (
        <div className="min-h-screen bg-black text-white p-6">
            {/* Header */}
            <div className="mb-8">
                <h1 className="text-2xl font-bold mb-1">User Management</h1>
                <p className="text-gray-400 text-sm">Manage system users and permissions</p>
            </div>

            {/* Filters and Actions */}
            <div className="flex gap-4 mb-6">
                {/* Search */}
                <div className="flex-1">
                    <div className="relative">
                        <svg className="absolute left-3 top-1/2 -translate-y-1/2 w-5 h-5 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" />
                        </svg>
                        <input
                            type="text"
                            placeholder="Search by name or email..."
                            value={searchQuery}
                            onChange={(e) => setSearchQuery(e.target.value)}
                            className="w-full bg-zinc-900 border border-zinc-800 rounded-lg pl-10 pr-4 py-2.5 text-sm focus:outline-none focus:border-zinc-700"
                        />
                    </div>
                </div>

                {/* Role Filter */}
                <select
                    value={roleFilter}
                    onChange={(e) => setRoleFilter(e.target.value)}
                    className="bg-zinc-900 border border-zinc-800 rounded-lg px-4 py-2.5 text-sm focus:outline-none focus:border-zinc-700 min-w-[150px]"
                >
                    <option>All Roles</option>
                    <option>Administrator</option>
                    <option>Field Supervisor</option>
                    <option>Engineer</option>
                </select>

                {/* Status Filter */}
                <select
                    value={statusFilter}
                    onChange={(e) => setStatusFilter(e.target.value)}
                    className="bg-zinc-900 border border-zinc-800 rounded-lg px-4 py-2.5 text-sm focus:outline-none focus:border-zinc-700 min-w-[150px]"
                >
                    <option>All Status</option>
                    <option>Active</option>
                    <option>Inactive</option>
                </select>

                {/* Create User Button */}
                <NextLink href="/user/new">
                    <Button className="bg-red-600 hover:bg-red-700 text-white px-6">
                        Create User
                    </Button>
                </NextLink>
            </div>

            {/* Table */}
            <div className="bg-zinc-900 rounded-lg border border-zinc-800 overflow-hidden">
                <table className="w-full">
                    <thead>
                        <tr className="border-b border-zinc-800">
                            <th className="text-left px-6 py-4 text-sm font-medium text-gray-400">Name</th>
                            <th className="text-left px-6 py-4 text-sm font-medium text-gray-400">Email</th>
                            <th className="text-left px-6 py-4 text-sm font-medium text-gray-400">Role</th>
                            <th className="text-left px-6 py-4 text-sm font-medium text-gray-400">Status</th>
                            <th className="text-left px-6 py-4 text-sm font-medium text-gray-400">Created Date</th>
                            <th className="text-left px-6 py-4 text-sm font-medium text-gray-400">Actions</th>
                        </tr>
                    </thead>
                    <tbody>
                        {paginatedUsers.map((user) => (
                            <tr key={user.id} className="border-b border-zinc-800 hover:bg-zinc-800/50 transition-colors">
                                <td className="px-6 py-4 text-sm font-medium">{user.name}</td>
                                <td className="px-6 py-4 text-sm text-gray-400">{user.email}</td>
                                <td className="px-6 py-4">
                                    <span className={`inline-flex items-center px-2.5 py-1 rounded text-xs font-medium border ${getRoleBadgeColor(user.role)}`}>
                                        {user.role}
                                    </span>
                                </td>
                                <td className="px-6 py-4">
                                    {user.status === 'Active' ? (
                                        <span className="inline-flex items-center gap-1.5 text-sm text-green-400">
                                            <span className="w-1.5 h-1.5 rounded-full bg-green-400"></span>
                                            Active
                                        </span>
                                    ) : (
                                        <span className="inline-flex items-center gap-1.5 px-2.5 py-1 rounded text-xs font-medium bg-gray-600/20 text-gray-400 border border-gray-600/30">
                                            Inactive
                                        </span>
                                    )}
                                </td>
                                <td className="px-6 py-4 text-sm text-gray-400">{user.createdDate}</td>
                                <td className="px-6 py-4">
                                    <div className="flex items-center gap-2">
                                        <NextLink href={`/user/${user.id}`}>
                                            <button className="p-1.5 hover:bg-zinc-800 rounded transition-colors" title="View">
                                                <svg className="w-4 h-4 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 12a3 3 0 11-6 0 3 3 0 016 0z" />
                                                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M2.458 12C3.732 7.943 7.523 5 12 5c4.478 0 8.268 2.943 9.542 7-1.274 4.057-5.064 7-9.542 7-4.477 0-8.268-2.943-9.542-7z" />
                                                </svg>
                                            </button>
                                        </NextLink>
                                        <NextLink href={`/user/${user.id}/edit`}>
                                            <button className="p-1.5 hover:bg-zinc-800 rounded transition-colors" title="Edit">
                                                <svg className="w-4 h-4 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M11 5H6a2 2 0 00-2 2v11a2 2 0 002 2h11a2 2 0 002-2v-5m-1.414-9.414a2 2 0 112.828 2.828L11.828 15H9v-2.828l8.586-8.586z" />
                                                </svg>
                                            </button>
                                        </NextLink>
                                        <button
                                            onClick={() => handleDelete(user.id, user.name)}
                                            className="p-1.5 hover:bg-zinc-800 rounded transition-colors"
                                            title="Delete"
                                        >
                                            <svg className="w-4 h-4 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 7l-.867 12.142A2 2 0 0116.138 21H7.862a2 2 0 01-1.995-1.858L5 7m5 4v6m4-6v6m1-10V4a1 1 0 00-1-1h-4a1 1 0 00-1 1v3M4 7h16" />
                                            </svg>
                                        </button>
                                    </div>
                                </td>
                            </tr>
                        ))}
                    </tbody>
                </table>
            </div>

            {/* Pagination */}
            <div className="flex items-center justify-between mt-4">
                <p className="text-sm text-gray-400">
                    Showing <span className="text-white">{(currentPage - 1) * itemsPerPage + 1}</span> to{' '}
                    <span className="text-white">{Math.min(currentPage * itemsPerPage, filteredUsers.length)}</span> of{' '}
                    <span className="text-white">{filteredUsers.length}</span> entries
                </p>

                <div className="flex items-center gap-2">
                    <button
                        onClick={() => setCurrentPage(Math.max(1, currentPage - 1))}
                        disabled={currentPage === 1}
                        className="px-3 py-1.5 rounded bg-zinc-900 border border-zinc-800 text-sm hover:bg-zinc-800 disabled:opacity-50 disabled:cursor-not-allowed"
                    >
                        ‹
                    </button>

                    {[...Array(totalPages)].map((_, i) => (
                        <button
                            key={i}
                            onClick={() => setCurrentPage(i + 1)}
                            className={`px-3 py-1.5 rounded text-sm ${currentPage === i + 1
                                ? 'bg-red-600 text-white'
                                : 'bg-zinc-900 border border-zinc-800 hover:bg-zinc-800'
                                }`}
                        >
                            {i + 1}
                        </button>
                    ))}

                    <button
                        onClick={() => setCurrentPage(Math.min(totalPages, currentPage + 1))}
                        disabled={currentPage === totalPages}
                        className="px-3 py-1.5 rounded bg-zinc-900 border border-zinc-800 text-sm hover:bg-zinc-800 disabled:opacity-50 disabled:cursor-not-allowed"
                    >
                        ›
                    </button>
                </div>
            </div>

            {/* Delete Confirmation Modal */}
            <ConfirmModal
                isOpen={deleteModal.isOpen}
                title="Delete User"
                message="Are you sure you want to delete"
                userName={deleteModal.userName}
                onConfirm={confirmDelete}
                onCancel={handleCancelDelete}
                isDanger={true}
                confirmText="Yes, Delete"
                cancelText="Cancel"
            />
        </div>
    );
}